// 拓展JQuery插件，用于监听元素宽度高度变化
;(function ($, window, undefined) {
    var elems = $([]),
        jq_resize = $.resize = $.extend($.resize, {}),
        timeout_id,
        str_setTimeout = 'setTimeout',
        str_resize = 'resize',
        str_data = str_resize + '-special-event',
        str_delay = 'delay',
        str_throttle = 'throttleWindow';
    jq_resize[str_delay] = 250;
    jq_resize[str_throttle] = true;

    $.event.special[str_resize] = {
        setup: function () {
            if (!jq_resize[str_throttle] && this[str_setTimeout]) {
                return false;
            }
            var elem = $(this);
            elems = elems.add(elem);
            $.data(this, str_data, {
                w: elem.width(),
                h: elem.height()
            });
            if (elems.length === 1) {
                loopy();
            }
        },
        teardown: function () {
            if (!jq_resize[str_throttle] && this[str_setTimeout]) {
                return false;
            }
            var elem = $(this);
            elems = elems.not(elem);
            elem.removeData(str_data);
            if (!elems.length) {
                clearTimeout(timeout_id);
            }
        },
        add: function (handleObj) {
            if (!jq_resize[str_throttle] && this[str_setTimeout]) {
                return false;
            }
            var old_handler;

            function new_handler(e, w, h) {
                var elem = $(this),
                data = $.data(this, str_data);
                data.w = w !== undefined ? w : elem.width();
                data.h = h !== undefined ? h : elem.height();
                old_handler.apply(this, arguments);
            }

            if ($.isFunction(handleObj)) {
                old_handler = handleObj;
                return new_handler;
            } else {
                old_handler = handleObj.handler;
                handleObj.handler = new_handler;
            }
        }
    };

    function loopy() {
        timeout_id = window[str_setTimeout](function () {
            elems.each(function () {
                var elem = $(this),
                    width = elem.width(),
                    height = elem.height(),
                    data = $.data(this, str_data);
                if (width !== data.w || height !== data.h) {
                    elem.trigger(str_resize, [data.w = width, data.h = height]);
                }
            });
            loopy();
        }, jq_resize[str_delay]);
    }
})(jQuery, this);

// 树图标拓展
;(function($, window, document, undefined) {

    "use strict";

    /* *****************************************************************************
     * Private functions and variables
     */

    function _getIcon(opts, type){
        return opts.map[type];
    }
    if($.ui && $.ui.fancytree){
        $.ui.fancytree.registerExtension({
            name: "awesome4",
            version: "4.4.0",
            // Default options for this extension.
            // options: {
            //     map: {
            //         _addClass: "fa ",
            //         checkbox: "fa-square-o",
            //         checkboxSelected: "fa-check-square-o",
            //         checkboxUnknown: "fa-square",
            //         dragHelper: "fa-arrow-right",
            //         dropMarker: "fa-long-arrow-right",
            //         error: "fa-warning",
            //         expanderClosed: "fa-caret-right",
            //         expanderLazy: "fa-angle-right",
            //         expanderOpen: "fa-caret-down",
            //         loading: "fa-spinner fa-pulse",
            //         nodata: "fa-meh-o",
            //         noExpander: "",
            //         radio: "fa-circle-thin",  // "fa-circle-o"
            //         radioSelected: "fa-circle",
            //         // Default node icons.
            //         // (Use tree.options.icon callback to define custom icons based on node data)
            //         doc: "fa-file-o",
            //         docOpen: "fa-file-o",
            //         folder: "fa-folder-o",
            //         folderOpen: "fa-folder-open-o"
            //     }
            // },

            treeInit: function(ctx){
                var tree = ctx.tree;
                this._superApply(arguments);
                tree.$container.addClass("fancytree-ext-glyph");
            },
            nodeRenderStatus: function(ctx) {
                var icon, res, span,
                    node = ctx.node,
                    $span = $(node.span),
                    opts = ctx.options.glyph,
                    map = opts.map;

                res = this._superApply(arguments);

                if( node.isRoot() ){
                    return res;
                }
                span = $span.children("span.fancytree-expander").get(0);
                if( span ){
                    // if( node.isLoading() ){
                        // icon = "loading";
                    if( node.expanded && node.hasChildren() ){
                        icon = "expanderOpen";
                    }else if( node.isUndefined() ){
                        icon = "expanderLazy";
                    }else if( node.hasChildren() ){
                        icon = "expanderClosed";
                    }else{
                        icon = "noExpander";
                    }
                    span.className = map["_addClass"] + map[icon];
                }

                if( node.tr ){
                    span = $("td", node.tr).find("span.fancytree-checkbox").get(0);
                }else{
                    span = $span.children("span.fancytree-checkbox").get(0);
                }
                if( span ){
                    icon = node.selected ? "checkboxSelected" : (node.partsel ? "checkboxUnknown" : "checkbox");
                    span.className = map["_addClass"] + map[icon];
                }

                // Standard icon (note that this does not match .fancytree-custom-icon,
                // that might be set by opts.icon callbacks)
                span = $span.children("span.fancytree-icon").get(0);
                if( span ){
                    if( node.statusNodeType ){
                        icon = _getIcon(opts, node.statusNodeType); // loading, error
                    }else if( node.folder ){
                        icon = node.expanded && node.hasChildren() ? _getIcon(opts, "folderOpen") : _getIcon(opts, "folder");
                    }else{
                        icon = node.expanded ? _getIcon(opts, "docOpen") : _getIcon(opts, "doc");
                    }
                    span.className = map["_addClass"] + icon;
                }
                return res;
            },
            nodeSetStatus: function(ctx, status, message, details) {
                var res, span,
                    opts = ctx.options.glyph,
                    node = ctx.node;

                res = this._superApply(arguments);

                if( status === "error" || status === "loading" || status === "nodata" ){
                    if(node.parent){
                        span = $("span.fancytree-expander", node.span).get(0);
                        if( span ) {
                            span.className = "fancytree-expander " + _getIcon(opts, status);
                        }
                    }else{ //
                        span = $(".fancytree-statusnode-" + status, node[this.nodeContainerAttrName])
                            .find("span.fancytree-icon").get(0);
                        if( span ) {
                            span.className = "fancytree-icon " + _getIcon(opts, status);
                        }
                    }
                }
                return res;
            }
        });
    }
        
}(jQuery, window, document));

/* IE兼容性代码 Start */
// 定义console变量,否则在IE下有可能因为未定义而报错
if(!window.console){
    window.console = {
        log : function(){},
        info: function(){},
        error: function(){},
        warn: function(){}
    };
}

// 兼容IE8判断数组方法
if (!Array.isArray) {
    Array.isArray = function(arg) {
        return Object.prototype.toString.call(arg) === '[object Array]';
    };
}

// 判断元素是否存在数组中，返回索引或者-1
if(!Array.indexOf){
    Array.prototype.indexOf = function(el){
        for (var i=0,n=this.length; i<n; i++){
            if (this[i] === el){
                return i;
            }
        }
        return -1;
    }  
}

/* IE兼容性代码 End */

/* 对象拓展代码 Start */
// 扩展contains方法：判断一个字符串是否包含一个子串,返回true或者false
if(!Array.contains){
    Array.prototype.contains = function(item){ 
        return RegExp("\\b"+item+"\\b").test(this); 
    }
}

if(!Array.insert){
    Array.prototype.insert = function (index, item) {  
        this.splice(index, 0, item);  
    };
}

